* {
  box-sizing: border-box;
}

body {
  letter-spacing: .04em;
  color: #2e2f42;
  margin: 16px;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
}

input, textarea {
  background-color: #0000;
  outline: #0000;
  font-family: inherit;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

img {
  object-fit: cover;
  width: 100%;
  display: block;
}

.search-form {
  text-align: center;
  margin-top: 36px;
}

#image-input {
  color: #2e2f42;
  border: 1px solid gray;
  border-radius: 4px;
  width: 272px;
  height: 40px;
  margin-right: 8px;
  padding-left: 16px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

#image-input:hover {
  border-color: #000;
}

#image-input:focus, #image-input:active {
  border-color: #4e75ff;
}

#image-input:-webkit-autofill {
  -webkit-text-fill-color: #000 !important;
  background-color: #fff !important;
  -webkit-box-shadow: inset 0 0 0 1000px #fff !important;
}

button {
  color: #fff;
  cursor: pointer;
  background: #4e75ff;
  border: none;
  border-radius: 8px;
  width: 91px;
  height: 40px;
  font-family: Montserrat, sans-serif;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button:hover {
  background: #6c8cff;
}

.gallery {
  flex-wrap: wrap;
  gap: 24px;
  margin: 32px 60px;
  display: flex;
}

.gallery-item {
  border: 1px solid gray;
  width: calc(33.3333% - 16px);
  max-height: 360px;
}

.descr-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px;
  display: flex;
}

.descr-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  background-image: linear-gradient(#4e75ff, #4e75ff), linear-gradient(#6c8cff, #6c8cff), linear-gradient(#6c8cff, #6c8cff);
  background-position: 0 0, 80px 20px, 100px 40px;
  background-repeat: no-repeat;
  background-size: 80px 70px, 30px 50px, 30px 30px;
  width: 130px;
  height: 100px;
  margin-top: 36px;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: content-box;
  background: gray;
  border: 10px solid #000;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  animation: .75s ease-in infinite alternate wheelSk;
  position: absolute;
  bottom: 10px;
  left: 12px;
  box-shadow: 78px 0 0 -10px gray, 78px 0 #000;
}

.loader:before {
  content: "";
  background-image: linear-gradient(gray 45px, #0000 0), linear-gradient(gray 45px, #0000 0), linear-gradient(gray 45px, #0000 0);
  background-position: 0 11px, 8px 35px, 0 60px;
  background-repeat: no-repeat;
  background-size: 30px 4px;
  width: 70px;
  height: 70px;
  animation: .75s linear infinite lineDropping;
  position: absolute;
  top: 0;
  right: 100%;
}

@keyframes wheelSk {
  0%, 50%, 100% {
    transform: translateY(0);
  }

  30%, 90% {
    transform: translateY(-3px);
  }
}

@keyframes lineDropping {
  0% {
    opacity: 1;
    background-position: 100px 11px, 115px 35px, 105px 60px;
  }

  50% {
    background-position: 0 11px, 20px 35px, 5px 60px;
  }

  60% {
    background-position: -30px 11px, 0 35px, -10px 60px;
  }

  75%, 100% {
    opacity: 0;
    background-position: -30px 11px, -30px 35px, -30px 60px;
  }
}
